
import {
  IonContent,
  IonHeader,
  IonTitle,
  IonToolbar,
  IonButton,
  IonItem,
  IonImg,
  IonLabel,
  modalController,
  IonRow,
} from "@ionic/vue";
import { defineComponent, reactive, toRefs } from "vue";
import { Cropper } from "vue-advanced-cropper";
import "vue-advanced-cropper/dist/style.css";

export default defineComponent({
  name: "ModalComponent",
  components: {
    IonContent,
    IonHeader,
    IonTitle,
    IonToolbar,
    IonButton,
    IonItem,
    Cropper,
    IonImg,
    IonLabel,
    IonRow,
  },
  props: {
    image: String,
  },
  setup() {
    const imageCropped = reactive({
      value: null,
    });
    async function recortar() {
      const { coordinates, canvas } = this.$refs.cropper.getResult();
      //Recorto la imagen del canvas y la convierto en un archivo jpeg y le gestiono la calidad con un número entre 0 y 1
      imageCropped.value = canvas.toDataURL('image/jpeg', 0.5);
    }
    return {
      ...toRefs(imageCropped),
      recortar,
    };
  },
  methods: {
      confirm() {
        return modalController.dismiss(this.value, 'confirm');
      },
    },
});
